import React, { lazy, Component } from "react";
import { CCol, CRow } from "@coreui/react";
import DateFnsUtils from "@date-io/date-fns";
import { TimePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import { useSelector } from "react-redux";
import BlocData from "./BlocData";

const Dashboard = ({}) => {
  const readings = useSelector((state) => state.readings);
  const listView = [
    { label: "Puissance Sortie 1", unit: "W", data: 1 },
    { label: "Puissance Sortie 2", unit: "W", data: 1 },
    { label: "Énergie Consommé / Jour", unit: "KWh", data: 1 },
    { label: "Économie", unit: "$", data: 1 },
    { label: "Pourcentage Batterie", unit: "%", data: 1 },
    { label: "Puissance Panneau Solaire", unit: "W", data: 1 },
  ];
  return (
    <div style={{ padding: "10px 100px" }}>
      <CRow>
        <CCol>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <TimePicker
              ampm={false}
              openTo="hours"
              views={["hours", "minutes", "seconds"]}
              format="HH:mm:ss"
              label="Update"
              value={readings ? Date(readings.timestamp) : null}
              disabled
            />
          </MuiPickersUtilsProvider>
        </CCol>
      </CRow>
      <CRow>
        <CCol>
          <BlocData label={"Puissance sortie 1"} data={readings && readings[7].values.Power} unit={"W"} />
        </CCol>
        <CCol>
          <BlocData label={"Puissance sortie 2"} data={readings && readings[8].values.Power} unit={"W"} />
        </CCol>
        <CCol>
          <BlocData label={"Pourcentage Batterie"} data={readings && readings[2].values.SoC} unit={"%"} />
        </CCol>
        <CCol>
          <BlocData label={"Puissance Panneau Solaire"} data={readings && readings[1].values.Power} unit={"W"} />
        </CCol>
      </CRow>
    </div>
  );
};

export default Dashboard;
